.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.motif-container {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.bg-1 {
		position: absolute;
		bottom: 5%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		z-index: 3;
		@include max-lg {
			width: 150%;
			bottom: 30%;
		}
		@include max-sm {
			bottom: 25%;
			width: 200%;
		}
	}
	.bg-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 30%);
		z-index: 3;
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 250%;
		}
	}
	.rumah {
		position: absolute;
		bottom: 5%;
		z-index: 3;
		@include max-sm {
			bottom: 8%;
		}
		&-1 {
			left: 0;
			width: 40%;
			@include max-lg {
				width: 60%;
				transform: translateX(-20%);
			}
		}
		&-2 {
			right: 5%;
			width: 32%;
			transform: scaleX(-1);
			@include max-lg {
				width: 50%;
				right: 0;
				transform: translateX(10%) scaleX(-1);
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: auto 1fr;
			width: 30%;
			height: 100%;
			margin: 0 auto;
			padding: 6rem 0 0;
			@include max-xl {
				width: 35%;
			}
			@include max-lg {
				width: 40%;
			}
			@include max-md {
				width: 60%;
			}
			@include max-sm {
				width: 70%;
			}
			.header-title {
				position: relative;
				text-align: center;
				z-index: 5;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
