.apology-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-dark);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.bg-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 70%;
		transform: translate(-50%, 10%);
		opacity: 0.2;
		@include max-lg {
			width: 90%;
		}
		@include max-sm {
			width: 130%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
