.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.bg-1 {
		position: absolute;
		bottom: 5%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			bottom: 30%;
			width: 150%;
		}
		@include max-sm {
			bottom: 25%;
			width: 200%;
		}
	}
	.bg-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 30%);
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 250%;
		}
	}
	.rumah {
		position: absolute;
		bottom: 5%;
		@include max-sm {
			bottom: 8%;
		}
		&-1 {
			left: 0;
			width: 30%;
			@include max-lg {
				width: 60%;
				transform: translateX(-20%);
			}
		}
		&-2 {
			right: 5%;
			width: 22%;
			transform: scaleX(-1);
			@include max-lg {
				right: 0;
				width: 50%;
				transform: translateX(10%) scaleX(-1);
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin-bottom: 2rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			border: 2px solid var(--border-color);
			border-radius: var(--border-radius);
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-main);
				opacity: 0.8;
				content: "";
				border-radius: var(--border-radius);
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
